@import url(https://fonts.googleapis.com/css?family=Sulphur+Point:400,700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Sulphur Point",sans-serif}

.App{background-image:url(/Clem-Fauchet/weather-app-test.git/static/media/cold-bg.c97cb84b.jpg);background-repeat:no-repeat;background-size:contain;background-position:bottom;-webkit-transition:0.4s ease-out;transition:0.4s ease-out}.App.warm{background-image:url(/Clem-Fauchet/weather-app-test.git/static/media/warm-bg.635ed87d.jpg)}.App main{margin:auto;min-height:100vh;padding:25px}.App main .search-box{display:flex;justify-content:center}.App main .search-box .search-bar{padding:15px;background:none;border:none;outline:none;background-color:rgba(255,255,255,0.5);border-radius:0px 0px 16px 16px;width:360px;margin-top:-25px;box-shadow:0px 5px rgba(0,0,0,0.2);color:#313131;font-size:20px;-webkit-transition:0.4s ease;transition:0.4s ease}.App main .search-box .search-bar:focus{background-color:rgba(255,255,255,0.75)}.App main .location-box,.App main .weather-box{text-align:center;min-height:20vh;display:flex;flex-direction:column;justify-content:center;color:#323246}.App main .location-box .location,.App main .weather-box .location{font-size:32px;font-weight:600;text-shadow:3px 3px ghostwhite}.App main .location-box .temp,.App main .weather-box .temp{background-color:rgba(255,255,255,0.5);margin:50px auto;padding:10px 38px;border-radius:15%;font-size:63px;font-weight:900;text-shadow:3px 3px ghostwhite;box-shadow:1px 2px rgba(0,0,0,0.2)}.App main .location-box .weather,.App main .weather-box .weather{font-size:42px;font-weight:600;text-shadow:2px 3px 3px ghostwhite}.App .App.warm .location{text-shadow:1px 0px ghostwhite}.App .App.warm .weather{text-shadow:0px 1px 1px ghostwhite}@media screen and (max-width: 520px){.App{background-size:cover}}


